import { loginUser, useAuthDispatch, useAuthState } from '../../context';
import React, { useState } from 'react';

import CustomBottomButton from '../common/BottomBtn';
import Header from '../layout/Header';
import LoginForm from './LoginForm';
import { isEmail } from './Validation';
import { useNavigate } from 'react-router-dom';

const LoginEmail = () => {
  //    ...?
  const [inputs, setInputs] = useState({
    email: '',
    password: '',
  });
  const { email, password } = inputs;
  const [isValidated, setValidation] = useState<boolean>(false);

  const { loading } = useAuthState(); // initialState     
  const dispatch = useAuthDispatch();
  const navigate = useNavigate();

  const handleLogin = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    if (isValidated) {
      try {
        const responseData = await loginUser(dispatch, inputs);

        if (!responseData.userId) return;
        //     
        // navigate('../createPaper', { replace: true });
      } catch (e) {
        console.error(e);
      }
    }
  };

  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setValidation(isEmail(email));
    setInputs(inputs => ({
      ...inputs,
      [name]: value,
    }));
  };

  return (
    <>
      <Header pageNm="" to="/login" />
      <LoginForm
        onChange={onChange}
        email={email}
        password={password}
        disabled={loading}
      />
      <CustomBottomButton
        text=""
        onclick={handleLogin}
        disabled={!isValidated}
      />
    </>
  );
};

// !
export default LoginEmail;
