import axios, { AxiosResponse } from 'axios';
import React, { useEffect } from 'react';
import BottomBtn from '../common/BottomBtn';
import { TextInput } from '../common/TextInput';
import EnvConfig from '../config/EnvConfig';
import { emailTest } from '../config/RegExp';
import { passVerify } from './SignUpFunction';
import { SignUpEmailInter } from './SignUpInterface';
import {
  clickNum,
  double,
  email,
  enterVerifyNum,
  veriftNum,
} from './signUpStore';

const SignUpEmail = ({
  doubleState,
  clickNumState,
  emailState,
  nicknameState,
  passwordState,
  enterVerifyState,
  verifyState,
  dispatch,
}: SignUpEmailInter) => {
  const emailVerify = async (event: any): Promise<void> => {
    event.preventDefault();
    try {
      const getDouble: AxiosResponse<object[]> = await axios({
        url: EnvConfig.DOUBLE_CHECK,
        method: 'get',
        params: {
          email: emailState,
        },
      });
      if (getDouble) {
        dispatch(double(true));
      }
    } catch (e) {
      dispatch(double(false));
      alert('   .');
    }
  };

  const mailSend = async (): Promise<void> => {
    try {
      if (clickNumState <= 0) {
        return alert('  ');
      } else {
        dispatch(clickNum());
        alert(
          `   .     : ${clickNumState}`
        );
        const codeSend = await axios({
          url: EnvConfig.VERIFY_MAIL,
          params: {
            email: emailState,
          },
        });
        dispatch(veriftNum(codeSend.data));
      }
    } catch (e) {
      console.log(e);
    }
  };

  useEffect(() => {
    if (doubleState) {
      mailSend();
    }
  }, [doubleState]);

  return (
    <>
      <div className="emailWrap">
        <TextInput
          title={''}
          htmlFor={'email'}
          placeholder={' '}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            dispatch(email(e.target.value))
          }
        />
      </div>
      <div className={doubleState ? '' : 'hidden'}>
        <TextInput
          title={''}
          htmlFor={'email'}
          placeholder={' '}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            dispatch(enterVerifyNum(e.target.value))
          }
        />
      </div>
      <BottomBtn
        text={doubleState ? ' ' : ' '}
        onclick={
          doubleState
            ? (e: any) =>
                passVerify(e, {
                  nicknameState,
                  passwordState,
                  emailTest,
                  emailState,
                  enterVerifyState,
                  verifyState,
                  doubleState,
                })
            : emailVerify
        }
      />
    </>
  );
};

export default SignUpEmail;
