import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { IBtnStyle } from '../interface/IStyled';

export interface IBtn {
  text: string;
  href?: string;
  link?: string;
  color?: string;
  background?: string;
  logo?: string;
}
/*
  
href:  (a  )
link:  ( )
*/
const Btn = ({ ...props }: IBtn) => {
  return (
    <>
      {props.href && <HrefBtn {...props} />}
      {props.link && <LinkBtn {...props} />}
    </>
  );
};

const HrefBtn = ({ ...props }: IBtn) => {
  return (
    <StyledBtn
      href={props.href}
      color={props.color}
      background={props.background}
    >
      {props.text}
      {props.logo && (
        <img src={`${process.env.PUBLIC_URL}/icons/${props.logo}`} alt="" />
      )}
    </StyledBtn>
  );
};

const LinkBtn = ({ ...props }: IBtn) => {
  const link = props.link ? props.link : '';
  return (
    <StyledLink to={link} color={props.color} background={props.background}>
      {props.text}
      {props.logo && (
        <img src={`${process.env.PUBLIC_URL}/icons/${props.logo}`} alt="" />
      )}
    </StyledLink>
  );
};

const StyledLink = styled(Link)<IBtnStyle>`
  display: flex;
  width: 15rem;
  margin: 1rem;
  padding: 1rem 1.5rem;
  border-radius: 60px;
  justify-content: space-between;
  background: ${props => props.background || 'initial'};
  color: ${props => props.color || 'initial'};
`;

const StyledBtn = styled.a<IBtnStyle>`
  display: flex;
  width: 15rem;
  margin: 1rem 1rem 0;
  padding: 1rem 1.5rem;
  border-radius: 60px;
  justify-content: space-between;
  background: ${props => props.background || 'initial'};
  color: ${props => props.color || 'initial'};
`;

export { Btn };
