export type LoginAction =
  | { type: 'REQUEST_LOGIN'; loading: boolean }
  | {
      type: 'LOGIN_SUCCESS';
      payload: any;
      // username: string;
      // user: string;
      // loading: boolean;
    }
  | { type: 'LOGOUT'; user: string }
  | { type: 'LOGIN_ERROR'; loading: boolean; error: any };

//    
export type LoginDispatch = Dispatch<LoginAction>;

export const AuthReducer = (initialState: IState, action: LoginAction) => {
  console.log(action);
  switch (action.type) {
    case 'REQUEST_LOGIN':
      return {
        ...initialState,
        loading: true,
      };
    case 'LOGIN_SUCCESS':
      //    key  
      return {
        ...initialState,
        user: action.payload,
        token: action.payload.idToken, // authNum?
        loading: false,
      };
    case 'LOGOUT':
      return {
        ...initialState,
        user: null,
        token: null,
      };
    case 'LOGIN_ERROR':
      return {
        ...initialState,
        loading: false,
        errorMsg: action.error,
      };
    default:
      return {
        ...initialState,
      };
    // throw new Error(`   : ${action}`);
  }
};
